import React from 'react';
import "./Portfolio.css"
import project from "./projects.json"
import { Rate } from 'antd';

import 'antd/dist/antd.css';


function Portfolio() {




    return (


        <div class="container-fluid">
            <h1 id="portfolio"  >Portfolio</h1>
            <div class="row justify-content-md-center justify-content-xs-center justify-content-lg-start">

                {project.map(data => (
                    <div class="col-lg-4 col-md-12 col-xs-12  text-center mb-5">

                        <a href={data.githubLink}>
                            <h3 id="h1"> {data.name} </h3>
                        </a>

                        <div class="card-body">
                            <a target="_blank" href={data.deployed}><img
                                src={data.picture} class="img-fluid" alt="Photo" />



                            </a>

                        </div>
                        <Rate allowHalf defaultValue={5} />
                    </div>
                ))}
            </div>
        </div>

    );
}

export default Portfolio;